"use client";
import React, { useRef } from "react";
import { RiMenu3Line } from "react-icons/ri";
import Header from "./Header";

const Navbar = () => {
  const menu = useRef();

  const menuhandler = () => {
    menu.current.classList.toggle("left-0");
    menu.current.classList.toggle("left-[-100%]");
  };

  const closeMenu = () => {
    if (window.innerWidth < 768) {
      menu.current.classList.remove("left-0");
      menu.current.classList.add("left-[-100%]");
    }
  };

  return (
    <nav className="flex items-center justify-between px-6 py-6 relative bg-green-900">
     
      <h1 className="font-bold text-2xl cursor-pointer text-gray-950">
        Guide-<span className="text-white">U</span>
      </h1>

      
      <div
        ref={menu}
        className="absolute top-[100%] left-[-100%] transition-all duration-300 ease-in-out
        flex flex-col md:flex-row gap-y-4 md:gap-x-10 items-start md:items-center
        md:static md:w-auto w-[200px] p-4 md:p-0 bg-green-100 md:bg-transparent z-50"
      >
        <Header name="Home" url="#Home" onClick={closeMenu} />
        <Header name="Services" url="#services" onClick={closeMenu} />
        <Header name="Pricing" url="#pricing" onClick={closeMenu} />
        <Header name="Contact" url="#Contact" onClick={closeMenu} />

       
        <a
          href="#login"
          onClick={closeMenu}
          className="block md:hidden text-[#060101] hover:text-white md:text-[13px] lg:text-[16px]"
        >
          Login
        </a>
      </div>

      
      <div className="flex items-center space-x-4">
        
        <button className="hidden md:block bg-green-500 p-2 rounded-[5px] hover:bg-white">
          Login
        </button>

        
        <RiMenu3Line
          className="text-white text-2xl md:hidden cursor-pointer"
          onClick={menuhandler}
        />
      </div>
    </nav>
  );
};

export default Navbar;
